<template>
  <div id="app">
    <div v-for="item in items" :key="item.key" :class="['test', {'active': item.isActive}]">
      <h2>{{item.name}}</h2>
      <label>Current Bid:</label>
      <input v-model="item.currentBid" @input="test($event, item)">
    </div>
  </div>
</template>
<script>
import Firebase from 'firebase';

const app = Firebase.initializeApp({
  projectId: 'auction-e82cf',
  databaseURL: 'https://databaseName.firebaseio.com',
});

const db = app.firestore();
db.settings({ timestampsInSnapshots: true }); // needing this to keep error from happening


// / ACTUAL VUE STUFF
export default{
  data() {
    return {
      items: [],
    };
  },
  firestore: {
    items: db.collection('items'),
  },
  methods: {
    test(e, item) {
      // Set the "capital" field of the city 'DC'
      return db.collection('items').doc(item.id).update({
        currentBid: e.target.value,
        isActive: true,
      })
        .then(() => {
          console.log('Document successfully updated!');

          // EW, REFACTOR
          setTimeout(() => {
            db.collection('items').doc(item.id).update({
              isActive: false,
            }).then(() => {
              console.log('Active successfully removed');
            })
              .catch((err) => {
                console.log('error removing active class...', err);
              });
          }, 7000); // every 7 seconds
        })
        .catch((error) => {
          // The document probably doesn't exist.
          console.error('Error updating document: ', error);
        });
    },

  },

};
</script>
<style>
.test{
  padding: 1rem;
  margin: 1rem;
  background: #dedede;
}
.active{
  background: lightgreen;
}
</style>
